export const dateHandler = (period, labels) => {
  switch(period){
      case '0': return labels.length + ' '
      case '1': return '14 '
      case '2': return ''
      case '3': return '3 '
      case '4': return ''
      case '5': return ' '
      default: return ' '
  }
}